@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@700&display=swap%27);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  margin: auto;
  background-color: #D3D3D3;
  text-align: center;
  min-height: 100vh;
  display: block;
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */ /* Konqueror HTML */ /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none;
}
input[type=checkbox], input[type=radio] {
  vertical-align: -2px;
  padding: 0;
  margin: 5px -5px;
  float:left;
  width: 100px;
}
label {
  float: left;
  margin: 5px px;
}
html, body {
  max-width: 100%;
  overflow-x: hidden;
}
.main {
  position: relative;
  width: 200px;
  height: 50px;
  background: #272727;
  cursor: pointer;
  border-radius: 5px;
  border: 2px solid white;
  overflow: hidden;
  font-family: 'Open Sans', sans-serif; 
}

.secondary {
  position: relative;
  width: 200px;
  height: 50px;
  background: #272727;
  cursor: pointer;
  border-radius: 5px;
  border: 2px solid white;
  overflow: hidden;
  color: white;
  font-family: 'Open Sans', sans-serif; 
}

.fill {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
}

.content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  color: white;
}

button{
  outline:none; 
  -webkit-tap-highlight-color: transparent;
}

input[type=text] {
  height: 2rem;
  padding: 2px 6px;
  margin: 8px 8px;
  display: inline-block;
  border: 1px solid #a3aab8;
  border-radius: 5px;
  box-sizing: border-box;
}

.item{
  font-size: 2rem;
  color: #6c778d;
}

.button{
  margin: auto 5px;
  height: 2rem;
  border: 1px solid #a3aab8;
  border-radius: 5px;
  box-sizing: border-box;
}

.textstyle{
  font-family: 'Open Sans', sans-serif; 
} 

